import { AppBar, Button, IconButton, makeStyles, TextField, Toolbar, Typography } from '@material-ui/core';
import React, { useCallback, useEffect, useReducer, useState } from 'react';
import { gapiLoad, isSignedIn, signIn, writeGasto } from '../api/sheetsApi';
import AccountIcon from '@material-ui/icons/AccountCircle'
import SaveButton from '../components/SaveButton';
//import NumberFormatCustom from '../components/NumberFormatCustom';

const useStyles = makeStyles((theme) => {
    return {
        title: {
            flexGrow: 1
        },
        input: {
            margin: theme.spacing(3),
        },
        buttonContainer: {
            display: 'flex',
            justifyContent: 'center',
        },
        form: {
            textAlign: 'center',
        },
    };
});

const initialFormValues = {
    description: '',
    category: '',
    amount: '',
};

function fieldsReducer(previous, action) {
    if (action.reset)
        return initialFormValues;
    return { ...previous, [action.name]: action.value };
}

const MainPage = () => {
    const classes = useStyles();
    const [fields, updateFields] = useReducer(fieldsReducer, initialFormValues);
    const [promise, setPromise] = useState();
    const [signedIn, setSignedIn] = useState(true);

    const handleFieldChange = useCallback((name) => ({ target: { value } }) => {
        updateFields({ name, value });
    }, [updateFields]);

    const handleClick = () => {
        const writePromise = writeGasto(fields.description, fields.category, fields.amount);
        writePromise.then(() => {
            updateFields({ reset: true });
        }).catch(() => { });
        setPromise(writePromise);
    }

    useEffect(() => {
        gapiLoad().then(() => {
            setSignedIn(isSignedIn());
        });
    }, []);

    const handleSignInClick = () => {
        signIn();
        setSignedIn(isSignedIn());
    }

    // const handleSignOutClick = () => {
    //     signOut();
    //     setSignedIn(isSignedIn());
    // }

    if (!signedIn) {
        return <div>no estás logueado
            <Button onClick={handleSignInClick}>Loguearse</Button>
        </div>
    }
    else {
        return (
            <div>
                <AppBar position="static">
                    <Toolbar>
                        <Typography variant="h6" className={classes.title}>
                            Nuevo gasto
                    </Typography>
                        <IconButton
                            aria-label="Usuario"
                            color="inherit"
                        >
                            <AccountIcon />
                        </IconButton>
                    </Toolbar>
                </AppBar>

                <form className={classes.form}>
                    <div className={classes.input}>
                        <TextField
                            label="Descripción"
                            value={fields.description}
                            onChange={handleFieldChange('description')}
                        />
                    </div>
                    <div className={classes.input}>
                        <TextField
                            label="Categoría"
                            value={fields.category}
                            onChange={handleFieldChange('category')}
                        />
                    </div>
                    <div className={classes.input}>
                        <TextField
                            label="Cantidad"
                            value={fields.amount}
                            onChange={handleFieldChange('amount')}
                        // InputProps={{
                        //     startAdornment: (
                        //         <InputAdornment position="start">
                        //             $
                        //         </InputAdornment>
                        //     ),
                        //     //inputComponent: NumberFormatCustom
                        // }}
                        />
                    </div>
                </form>
                <SaveButton
                    onClick={handleClick}
                    promise={promise}
                />
            </div>
        );
    }
};

export default MainPage;
