import React, { useEffect, useState } from 'react';
import {
    CircularProgress, Fab, Fade, makeStyles
} from '@material-ui/core';
import SaveIcon from '@material-ui/icons/Save';
import CheckIcon from '@material-ui/icons/Check';
import ErrorIcon from '@material-ui/icons/Close';
import { green, red } from '@material-ui/core/colors';
import clsx from 'clsx';

const useStyles = makeStyles((theme) => ({
    buttonContainer: {
        display: 'flex',
        justifyContent: 'center',
    },
    wrapper: {
        margin: theme.spacing(1),
        position: 'absolute',
    },
    success: {
        backgroundColor: green[500],
        '&:hover': {
            backgroundColor: green[700]
        }
    },
    failure: {
        backgroundColor: red[500],
        '&:hover': {
            backgroundColor: red[700]
        }
    },
    progress: {
        color: green[500],
        position: 'absolute',
        top: -6,
        left: -6,
        zIndex: 1,
    },
    iconWrapper: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        '& *': {
            position: 'absolute'
        }
    }
}));

const buttonStates = {
    idle: 0, loading: 1, success: 2, failure: 3
};

const SaveButton = ({
    onClick,
    promise,
}) => {
    const classes = useStyles();
    const [state, setState] = useState(buttonStates.idle);

    useEffect(() => {
        if (promise) {
            setState(buttonStates.loading);
            promise.then(() => {
                setState(buttonStates.success)
                setTimeout(() => { setState(buttonStates.idle); }, 2000);
            }
            ).catch(() => {
                setState(buttonStates.failure);
                setTimeout(() => { setState(buttonStates.idle); }, 2000);
            });
        }
        else {
            setState(buttonStates.idle);
        }
    }, [promise]);

    return (
        <div className={classes.buttonContainer}>
            <div className={classes.wrapper}>
                <Fab
                    aria-label="Guardar"
                    className={clsx({
                        [classes.success]: state === buttonStates.success,
                        [classes.failure]: state === buttonStates.failure
                    })}
                    color="primary"
                    onClick={onClick}
                    disabled={state === buttonStates.loading}
                >
                    <div className={classes.iconWrapper}>
                        <Fade
                            in={state === buttonStates.idle || state === buttonStates.loading}
                            appear={false}
                        >
                            <SaveIcon />
                        </Fade>
                        <Fade
                            in={state === buttonStates.success}
                            style={{
                                transitionDelay: state === buttonStates.success ? '200ms' : '0ms'
                            }}
                        >
                            <CheckIcon />
                        </Fade>
                        <Fade
                            in={state === buttonStates.failure}
                            style={{
                                transitionDelay: state === buttonStates.failure ? '200ms' : '0ms'
                            }}
                        >
                            <ErrorIcon />
                        </Fade>
                    </div>
                    {/* {state === 'success' ? <CheckIcon /> : <SaveIcon />} */}
                </Fab>
                {
                    state === buttonStates.loading
                    && <CircularProgress size={68} className={classes.progress} />
                }
            </div>
        </div>
    );
};

export default SaveButton;
