const clientId = '919469260883-sg2gn8vm4pf9dnu8dbomd43o3bg6ks8c.apps.googleusercontent.com';
const apiKey = 'AIzaSyCi-9_coki0wNUyCpwzbQNRO1NnptWd4zY';
const discoveryDocs = ["https://sheets.googleapis.com/$discovery/rest?version=v4"];
const scopes = "https://www.googleapis.com/auth/spreadsheets";

/**
 *  On load, called to load the auth2 library and API client library.
 */
export function gapiLoad() {
    return new Promise((resolve, reject) => {
        window.gapi.load('client:auth2', initClient(resolve, reject));
    });
}

/**
 *  Initializes the API client library and sets up sign-in state listeners.
 */
export function initClient(resolve, reject) {
    return function (signInCallback) {
        window.gapi.client.init({
            apiKey: apiKey,
            clientId: clientId,
            discoveryDocs: discoveryDocs,
            scope: scopes
        }).then(() => {
            // Listen for sign-in state changes.
            window.gapi.auth2.getAuthInstance().isSignedIn.listen(signInCallback);

            // Handle the initial sign-in state.
            if (signInCallback)
                signInCallback(window.gapi.auth2.getAuthInstance().isSignedIn.get());

            resolve();
        }, (error) => {
            reject();
            throw new Error(JSON.stringify(error, null, 2));
        });
    }
}

export function isSignedIn() {
    return window.gapi.auth2.getAuthInstance().isSignedIn.get();
}

