export function signIn() {
    window.gapi.auth2.getAuthInstance().signIn();
}

export function signOut() {
    window.gapi.auth2.getAuthInstance().signOut();
}

export async function getGastos() {
    try {
        const response = await window.gapi.client.sheets.spreadsheets.values.get({
            spreadsheetId: '1IP_zrL81Ej2TCLD5qIYWdP8fFk3xYYvJ_-kogZWAa9E',
            range: 'Gastos!A2:B',
        });
        const range = response.result;
        let out = 'Apellido, Nombre:';
        if (range.values.length > 0) {
            for (let i = 0; i < range.values.length; i++) {
                const row = range.values[i];
                out += row[1] + ', ' + row[0];
            }
        } else {
            out = 'No data found.';
        }
        return out;
    }
    catch {
        throw new Error('Error haciendo spreadsheets.values.get');
    }
}

export async function writeGasto(description, category, amount) {
    if (isNullOrEmpty(description)) throw new Error('description is not valid');
    if (isNullOrEmpty(category)) throw new Error('category is not valid');
    if (isNullOrEmpty(amount)) throw new Error('amount is not valid');

    try {
        await window.gapi.client.sheets.spreadsheets.values.append({
            spreadsheetId: '1IP_zrL81Ej2TCLD5qIYWdP8fFk3xYYvJ_-kogZWAa9E',
            range: 'Gastos!A2:B',
            valueInputOption: 'USER_ENTERED',
            resource: {
                values: [[description, category, amount]]
            }
        });
    }
    catch (error) {
        throw new Error(error);
    }
}

function isNullOrEmpty(value) {
    return value === null || value === undefined || (typeof value === 'string' && value.length === 0);
}
